import React, {useRef} from "react";
import "../Style/Home.css";
import Contact from "./Contact";
import WhyWed from "./WhyWed";
import How from "./How";
import Document from "./Document";
import Terms from "./Terms";
import Footer from "./Footer";
import Alert from "./Alert";
import Wedding from '../Component/Wedding'
import { useAlert } from "../Context/AlertContext";
import Lending from "./Lending";
import Reject from "./Reject";

const Home = () =>{
    const { showAlert, reject } = useAlert();
    const inputRefName = useRef(null)
    return(
        <>
        { showAlert ? (<Alert/>) : reject ? (<Reject />) :
        (
        <>
            <div id="contact">
            <Contact inputRefName={inputRefName}/>
            </div>
            <WhyWed />
            <How inputRefName={inputRefName}/>
            <Terms />
            <Document />
            <Lending />
            < Wedding inputRefName={inputRefName} />
            <div id="footer">
            <Footer />
            </div>
        </>
        )}
        </>
    )
}

export default Home;