import React from "react";
import { Swiper, SwiperSlide } from 'swiper/react';
import '../js/main'
import 'swiper/css';
import '../Style/Footer.css'
import { Autoplay } from 'swiper/modules';
import imageArray from "../js/Image";

const Wedding = ({ inputRefName }) => {

    const handleClick = () => {
        if (inputRefName && inputRefName.current) {
            inputRefName.current.focus();
        }
        else {
            window.location.href = '/'
        }
    }
    return (
        <>
            <div className="text-center text-dark mt-5" id="testimonials" style={{ background: "#F4EBEB" }}>
                <div className="container p-4">
                    <section className="mb-4">
                        <h2 className="text-start mt-5" style={{ fontFamily: "Montserrat", fontWeight: "600" }}>Do you need to finance your<span style={{ color: "#6C0505" }}> Wedding Business?</span></h2>
                        <p className="text-start mt-2" style={{ fontFamily: "Normal", fontWeight: "500", fontSize: "1.375rem" }}>Don’t let liquidity challenges hold you back from growing your business.</p>
                    </section>
                </div>

                <div className="container-fluid">
                    <div className="row">
                        <div className="col-lg-12">
                            <Swiper
                                spaceBetween={0}
                                slidesPerView={3}
                                modules={[Autoplay]}
                                autoplay={{
                                    delay: 2500,
                                    disableOnInteraction: false,
                                }}
                                loop="true"
                            >
                                {imageArray.map((image, index) => (
                                    <SwiperSlide key={index}>
                                        <img src={image} className="img-fluid imgHW" alt={`thumb${index}`} />
                                    </SwiperSlide>
                                ))}
                            </Swiper>
                        </div>
                    </div>
                    <button onClick={handleClick} className="btn btn-outline mb-5 mt-3" style={{ background: "#6C0505", color: "#fff" }}>Apply for Credit Line</button>
                </div>
            </div>
        </>
    )
}

export default Wedding